"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Navbar from "@/components/Navbar";
import Footer from "@/components/Footer";
import toast, { Toaster } from "react-hot-toast";

interface Category { 
  id: string; 
  name: string; 
  icon: string; 
  description: string;
}

interface Company {
  id: string;
  name: string;
  logo_url: string | null;
  category_id: string;
}

// Mock ThreeJSBackground component
const ThreeJSBackground = () => {
  return (
    <div className="fixed inset-0 -z-10 bg-gradient-to-br from-blue-900/20 to-purple-900/20">
      <div className="absolute inset-0 opacity-20 bg-grid-pattern"></div>
    </div>
  );
};

const DashboardPage = () => {
  const router = useRouter();
  const [name, setName] = useState("");
  const [logo, setLogo] = useState<File | null>(null);
  const [categoryId, setCategoryId] = useState("");
  const [companies, setCompanies] = useState<Company[]>([]);
  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);

  // Main categories data
  const mainCategories = [
    {
      id: "life-insurance",
      name: "Life Insurance",
      icon: "ðŸ›¡ï¸",
      description: "Coverage tailored to your life goals."
    },
    {
      id: "bonds",
      name: "Bonds",
      icon: "ðŸ“ˆ",
      description: "Stable income with secure bond investments."
    },
    {
      id: "loan",
      name: "Loan",
      icon: "ðŸ’°",
      description: "Flexible loans for personal and business needs."
    },
    {
      id: "upcoming-nfo",
      name: "Upcoming NFO",
      icon: "ðŸš€",
      description: "Discover new fund opportunities."
    },
    {
      id: "mutual-funds",
      name: "Mutual Funds",
      icon: "ðŸ“Š",
      description: "Grow wealth with disciplined investing."
    },
    {
      id: "digital-gold",
      name: "Digital Gold",
      icon: "ðŸ¥‡",
      description: "Invest in gold digitally with ease."
    }
  ];

  useEffect(() => {
    if (!localStorage.getItem("isAdmin")) {
      router.push("/admin/login");
      return;
    }
    initializeData();
  }, [router]);

  const initializeData = async () => {
    setLoading(true);
    try {
      await fetch("/api/admin/seed-categories", { method: "POST" });
      await Promise.all([fetchCategories(), fetchCompanies()]);
    } catch (error) {
      toast.error("Failed to initialize data");
    } finally {
      setLoading(false);
    }
  };

  const fetchCategories = async () => {
    try {
      const res = await fetch("/api/admin/categories");
      const result = await res.json();
      if (result.categories) {
        setCategories(result.categories);
        if (!categoryId && result.categories.length > 0) {
          setCategoryId(result.categories[0].id);
        }
      }
    } catch (error) {
      toast.error("Failed to fetch categories");
    }
  };

  const fetchCompanies = async () => {
    try {
      const res = await fetch("/api/admin/companies");
      const result = await res.json();
      if (result.data) {
        setCompanies(result.data);
      } else {
        toast.error(result.error || "Failed to fetch companies");
      }
    } catch (error) {
      toast.error("Failed to fetch companies");
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      let logoUrl = null;
      if (logo) {
        const formData = new FormData();
        formData.append("file", logo);
        const uploadRes = await fetch("/api/admin/upload", { 
          method: "POST", 
          body: formData 
        });
        const uploadResult = await uploadRes.json();
        if (uploadResult.error) {
          toast.error(uploadResult.error);
          return;
        }
        logoUrl = uploadResult.url;
      }
      
      const res = await fetch("/api/admin/companies", { 
        method: "POST", 
        headers: { "Content-Type": "application/json" }, 
        body: JSON.stringify({ name, logoUrl, categoryId }) 
      });
      const result = await res.json();
      
      if (result.error) {
        toast.error(result.error);
        return;
      }
      
      setName(""); 
      setLogo(null);
      toast.success("Company added!");
      fetchCompanies();
    } catch (error) {
      toast.error("Failed to add company");
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm("Are you sure you want to delete this company?")) return;
    
    const toastId = toast.loading("Deleting...");
    try {
      const res = await fetch(`/api/admin/companies/${id}`, { method: "DELETE" });
      const result = await res.json();
      
      if (result.success) {
        toast.success("Company deleted!", { id: toastId });
      } else {
        toast.error(result.error || "Delete failed", { id: toastId });
      }
      fetchCompanies();
    } catch (error) {
      toast.error("Delete failed", { id: toastId });
    }
  };

  const handleUpdateLogo = async (id: string) => {
    const fileInput = document.createElement("input");
    fileInput.type = "file";
    fileInput.accept = "image/*";
    
    fileInput.onchange = async (e) => {
      const file = (e.target as HTMLInputElement).files?.[0] || null;
      if (!file) return;
      
      const toastId = toast.loading("Updating logo...");
      try {
        const formData = new FormData();
        formData.append("file", file);
        
        const uploadRes = await fetch("/api/admin/upload", {
          method: "POST",
          body: formData
        });
        const uploadResult = await uploadRes.json();
        
        if (uploadResult.error) {
          toast.error(uploadResult.error, { id: toastId });
          return;
        }
        
        const res = await fetch(`/api/admin/companies/${id}`, { 
          method: "PUT", 
          headers: { "Content-Type": "application/json" }, 
          body: JSON.stringify({ logoUrl: uploadResult.url }) 
        });
        const result = await res.json();
        
        if (result.success) {
          toast.success("Logo updated!", { id: toastId });
        } else {
          toast.error(result.error || "Update failed", { id: toastId });
        }
        fetchCompanies();
      } catch (error) {
        toast.error("Update failed", { id: toastId });
      }
    };
    
    fileInput.click();
  };

  const handleCategoryClick = (categoryId: string) => {
    setSelectedCategory(categoryId);
    // Here you would typically fetch subcategories for the selected category
  };

  if (loading) {
    return (
      <>
        <Navbar />
        <main className="min-h-screen flex items-center justify-center">
          <div className="text-lg">Loading...</div>
        </main>
        <Footer />
        <Toaster />
      </>
    );
  }

  return (
    <>
      <Navbar />
      <ThreeJSBackground />
      <main className="min-h-screen py-8 relative z-10">
        <div className="container mx-auto px-4 max-w-6xl">
          <Toaster position="top-right" />
          
          <div className="mb-8 text-center">
            <h1 className="text-3xl font-bold text-gray-800">Admin Dashboard</h1>
            <p className="text-gray-600 mt-2">Manage insurance companies and categories</p>
          </div>

          {/* Main Categories Grid */}
          <div className="mb-12">
            <h2 className="text-2xl font-semibold text-gray-800 mb-6 text-center">Our Services</h2>
            <p className="text-gray-600 text-center mb-8 max-w-2xl mx-auto">
              Comprehensive solutions across protection, health, and long-term wealth creation.
            </p>
            
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {mainCategories.map((category) => (
                <div 
                  key={category.id} 
                  className="bg-white rounded-lg shadow-sm p-6 border border-gray-100 hover:shadow-md transition-all cursor-pointer group"
                  onClick={() => handleCategoryClick(category.id)}
                >
                  <div className="flex flex-col items-center text-center">
                    <div className="text-4xl mb-4 group-hover:scale-110 transition-transform">
                      {category.icon}
                    </div>
                    <h3 className="text-xl font-semibold text-gray-800 mb-2">{category.name}</h3>
                    <div className="w-12 h-1 bg-blue-500 mb-4 mx-auto"></div>
                    <p className="text-gray-600 text-sm">{category.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Add Company Form - Only show if a category is selected */}
          {selectedCategory && (
            <div className="bg-white rounded-xl shadow-sm p-6 mb-10 border border-gray-100">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-semibold text-gray-800">
                  Add New Company to {mainCategories.find(c => c.id === selectedCategory)?.name}
                </h2>
                <button 
                  onClick={() => setSelectedCategory(null)}
                  className="px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition cursor-pointer"
                >
                  Back to Categories
                </button>
              </div>
              <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-5">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Company Name</label>
                  <input 
                    type="text" 
                    placeholder="Enter company name" 
                    className="w-full border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition" 
                    value={name} 
                    onChange={(e) => setName(e.target.value)} 
                    required 
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Subcategory</label>
                  <select 
                    className="w-full border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition cursor-pointer" 
                    value={categoryId} 
                    onChange={(e) => setCategoryId(e.target.value)} 
                    required
                  >
                    <option value="">Select a subcategory</option>
                    {categories.filter(cat => cat.name.includes(mainCategories.find(c => c.id === selectedCategory)?.name || ""))
                      .map(cat => (
                        <option key={cat.id} value={cat.id}>
                          {cat.icon} {cat.name}
                        </option>
                      ))
                    }
                  </select>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Company Logo</label>
                  <input 
                    type="file" 
                    accept="image/*" 
                    className="w-full border border-gray-300 rounded-lg px-4 py-2 text-sm file:mr-4 file:py-2 file:px-4 file:rounded-lg file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100 transition cursor-pointer" 
                    onChange={e => setLogo(e.target.files?.[0] || null)} 
                  />
                </div>
                
                <div className="flex items-end">
                  <button 
                    type="submit" 
                    className="w-full bg-blue-600 text-white py-3 px-4 rounded-lg hover:bg-blue-700 transition disabled:bg-gray-400 disabled:cursor-not-allowed cursor-pointer" 
                    disabled={!categoryId}
                  >
                    Add Company
                  </button>
                </div>
              </form>
            </div>
          )}

          {/* Companies List - Only show if a category is selected */}
          {selectedCategory && (
            <div>
              <h2 className="text-2xl font-semibold text-gray-800 mb-6">Existing Companies in {mainCategories.find(c => c.id === selectedCategory)?.name}</h2>
              
              {categories
                .filter(category => category.name.includes(mainCategories.find(c => c.id === selectedCategory)?.name || ""))
                .map(category => {
                const categoryCompanies = companies.filter(company => company.category_id === category.id);
                
                if (categoryCompanies.length === 0) return null;
                
                return (
                  <div key={category.id} className="mb-10">
                    <div className="flex items-center mb-4">
                      <span className="text-2xl mr-2">{category.icon}</span>
                      <h3 className="text-xl font-semibold text-gray-800">{category.name}</h3>
                      <span className="ml-3 bg-blue-100 text-blue-800 text-sm font-medium px-3 py-1 rounded-full">
                        {categoryCompanies.length} companies
                      </span>
                    </div>
                    
                    <div className="bg-white rounded-xl shadow-sm overflow-hidden border border-gray-100">
                      <table className="w-full">
                        <thead className="bg-gray-50">
                          <tr>
                            <th className="text-left py-4 px-6 font-medium text-gray-600">Logo</th>
                            <th className="text-left py-4 px-6 font-medium text-gray-600">Company Name</th>
                            <th className="text-right py-4 px-6 font-medium text-gray-600">Actions</th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-100">
                          {categoryCompanies.map(company => (
                            <tr key={company.id} className="hover:bg-gray-50 transition">
                              <td className="py-4 px-6">
                                {company.logo_url ? (
                                  <img 
                                    src={company.logo_url} 
                                    alt={company.name} 
                                    className="w-12 h-12 object-contain mx-auto" 
                                  />
                                ) : (
                                  <div className="w-12 h-12 bg-gray-100 rounded-lg flex items-center justify-center mx-auto">
                                    <span className="text-gray-400 text-xs">No Logo</span>
                                  </div>
                                )}
                              </td>
                              <td className="py-4 px-6 font-medium text-gray-800">{company.name}</td>
                              <td className="py-4 px-6">
                                <div className="flex justify-end space-x-3">
                                  <button 
                                    className="px-4 py-2 bg-blue-100 text-blue-700 rounded-lg hover:bg-blue-200 transition cursor-pointer"
                                    onClick={() => handleUpdateLogo(company.id)}
                                  >
                                    Update Logo
                                  </button>
                                  <button 
                                    className="px-4 py-2 bg-red-100 text-red-700 rounded-lg hover:bg-red-200 transition cursor-pointer"
                                    onClick={() => handleDelete(company.id)}
                                  >
                                    Delete
                                  </button>
                                </div>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>
      </main>
      <Footer />
      
      <style jsx>{`
        .bg-grid-pattern {
          background-image: 
            linear-gradient(rgba(255, 255, 255, 0.05) 1px, transparent 1px),
            linear-gradient(90deg, rgba(255, 255, 255, 0.05) 1px, transparent 1px);
          background-size: 20px 20px;
        }
      `}</style>
    </>
  );
};

export default DashboardPage;